<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ resource.name }}</div>

          <div v-html="resource.description"></div>

          <v-img class="mb-3" :src="require('../../../assets/agree.svg')" contain height="240"/>

          <v-btn @click="goToData" class="mb-2" color="primary" block>Iniciar proceso</v-btn>
        </v-col>
      </v-row>

      <button-back></button-back>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";
import ButtonBack from "../../../components/ButtonBack";

export default {
  name: 'DocumentsDetailVariable',
  components: {ButtonBack},
  data: () => ({
    resource: '',
  }),
  computed: {
    ...mapGetters(['user']),
    resourceId() {
      return this.$route.params.id
    }
  },
  created() {
    this.loadResource()
  },
  methods: {
    loadResource() {
      let url = 'resources/' + this.resourceId
      axios.get(url).then((response) => {
        this.resource = response.data
      })
    },
    goToData() {
      this.isLoading = true

      let url = 'operations';
      let data = {
        resource_id: this.resourceId,
        payment_method_id: 0,
        status_id: 1,
      }

      axios.post(url, data).then((response) => {

        this.$router.push({name: 'DocumentsData', params: {id: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>
